import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage implements OnInit{

  startScreen : boolean = false;
  listenScreen : boolean = false;
  noResults : boolean = false;
  resultsConcept1 : boolean = false;
  resultsConcept2 : boolean = false;

  ngOnInit() {
    setTimeout(() => {
      this.startScreen = !this.startScreen;
    }, 500);
  }

  startListen() {
    this.startScreen = ! this.startScreen;
    this.listenScreen = ! this.listenScreen;

  }

  displayResults(){
    this.listenScreen = ! this.listenScreen;
    this.resultsConcept2 = ! this.resultsConcept2;
  }

  goHome(){
    this.startScreen = ! this.startScreen;
    this.resultsConcept1 = ! this.resultsConcept1;
  }

  goHome2(){
    this.startScreen = ! this.startScreen;
    this.resultsConcept2 = ! this.resultsConcept2;
  }

}